export default {
	props: {
		musicName: {
			type: String,
			default: '精选歌单'
		},
		currentList: {
			type: Array
		},
		type:{
			type:String,
			default:''
		}
	},
	methods: {
		filterPlayCount(val) {
			if (!val || val <= 0) return 0
			else if (val < 1000) return val
			else if (val >= 1000 && val < 10000) return Math.ceil(val / 1000) + '千'
			else if(val>=10000&&val<100000000) return Math.ceil(val / 10000) + '万'
			return Math.ceil(val / 100000000) + '亿'
		}
	}
}
